import React, { useState } from "react";
import {
  Stack,
  useMediaQuery,
  Button,
  Typography,
  TextField,
  MenuItem,
} from "@mui/material";
import { useFormikContext } from "formik";
import { useDispatch, useSelector } from "react-redux";

import { colors } from "../../../../../../utils/colors";
import {
  dialogButtonStyle,
} from "../../../../../../utils/commonStyle";
import DragAndDrop from "./DragAndDrop";
import { setLoading } from "../../../../../../redux/features/appSlice";
import { appapi } from "../../../../../../api/apiClient";
import APPCONFIG from "../../../../../../config/apiConfig";

export default function EcoCampaignAdditionalInfo() {
  const isMobile = useMediaQuery("(max-width:1000px)");

  const dispatch = useDispatch();

  const { citiesList } = useSelector((state) => state.app);

  const { user } = useSelector((state) => state.auth);

  const [file, setFile] = useState(null);

  const {
    values,
    touched,
    errors,
    handleChange,
    handleBlur,
    dirty,
    setFieldValue,
  } = useFormikContext();

  //handle upload will do the upload in aws
  const handleUploadClick = async (e) => {
    dispatch(setLoading(true));
    const ext = file.name.split(".")[1];
    const key = `enterprise/${user?.enterpriseId}/${Date.now()}.${ext}`;
    const signedUrlResult = await appapi.post("/s3", {
      fileName: key,
      contentType: file.type,
    });
    if (signedUrlResult.ok) {
      const signedUrl = signedUrlResult.data;
      fetch(signedUrl, { method: "PUT", body: file })
        .then((res) => {
          if (res.ok) {
            setFieldValue("brandLogo", key);
          } else {
          }
          dispatch(setLoading(false));
        })
        .catch((error) => {
          dispatch(setLoading(false));
          console.log(
            "There has been a problem with fetching signed url: " +
              error.message
          );
        });
    } else {
    }
  };

  const handleFile = (file) => {
    if (file) {
      setFile(file);
    } else {
      setFieldValue("brandLogo", '');
      setFile(null);
    }
  };

  return (
    <Stack className={isMobile ? "mt-14": ""}>
      <DragAndDrop url={ values.brandLogo ? APPCONFIG.s3Path + values.brandLogo: null} id="brandLogo" handleFile={(files) => handleFile(files)} />
      <Button
        disabled={!file}
        onClick={handleUploadClick}
        sx={{ ...dialogButtonStyle, marginTop: "8px", width: "100%" }}
        variant="contained"
      >
        Upload
      </Button>
      {touched.brandLogo && Boolean(errors.brandLogo) && <Typography className="err-msg">
        {(touched.brandLogo || dirty.brandLogo) && errors.brandLogo}
      </Typography>}
      <Stack
        className="mt-16"
        direction={isMobile ? "column": "row"}
        justifyContent="space-between"
        alignItems="flex-start"
        spacing="8px"
      >
        <Stack flex={1} style={isMobile ? { width: '100%'} : {}}>
          <Typography
            className="fs-14 ff-Roboto fw-medium mb-7"
            color={colors.blackMain}
          >
            Brand
          </Typography>
          <TextField
            fullWidth
            id="brandName"
            name="brandName"
            value={values.brandName || ""}
            onChange={handleChange}
            onBlur={handleBlur}
            error={touched.brandName && Boolean(errors.brandName)}
            helperText={
              (touched.brandName || dirty.brandName) && errors.brandName
            }
          />
        </Stack>
        <Stack flex={1} style={isMobile ? { width: '100%'} : {}}>
          <Typography
            className="fs-14 ff-Roboto fw-medium mb-7"
            color={colors.blackMain}
          >
            Sub Brand
          </Typography>
          <TextField
            fullWidth
            id="subBrandName"
            name="subBrandName"
            value={values.subBrandName || ""}
            onChange={handleChange}
            onBlur={handleBlur}
            error={touched.subBrandName && Boolean(errors.subBrandName)}
            helperText={
              (touched.subBrandName || dirty.subBrandName) &&
              errors.subBrandName
            }
          />
        </Stack>
      </Stack>
      <Stack className="mt-16">
        <Typography
          className="fs-14 ff-Roboto fw-medium mb-7"
          color={colors.blackMain}
        >
          Product
        </Typography>
        <TextField
          fullWidth
          id="product"
          name="product"
          value={values.product || ""}
          onChange={handleChange}
          onBlur={handleBlur}
          error={touched.product && Boolean(errors.product)}
          helperText={(touched.product || dirty.product) && errors.product}
        />
      </Stack>
      <Stack className="mt-16">
        <Typography
          className="fs-14 ff-Roboto fw-medium mb-7"
          color={colors.blackMain}
        >
          City
        </Typography>
        <TextField
          fullWidth
          id="cityId"
          name="cityId"
          value={values.cityId || ""}
          onChange={(e) => {
            handleChange(e);
          }}
          onBlur={handleBlur}
          error={touched.cityId && Boolean(errors.cityId)}
          helperText={(touched.cityId || dirty.cityId) && errors.cityId}
          select
        >
          {citiesList.map((item) => (
            <MenuItem key={item.cityId} value={item.cityId}>
              {" "}
              {item.cityName}{" "}
            </MenuItem>
          ))}
        </TextField>
      </Stack>
      <Stack className="mt-16">
        <Typography
          className="fs-14 ff-Roboto fw-medium mb-7"
          color={colors.blackMain}
        >
          Country
        </Typography>
        <TextField
          fullWidth
          id="country"
          name="country"
          value={values.country || ""}
          onChange={handleChange}
          onBlur={handleBlur}
          error={touched.country && Boolean(errors.country)}
          helperText={(touched.country || dirty.country) && errors.country}
        />
      </Stack>
    </Stack>
  );
}
